.form-input::placeholder,
.form-textarea::placeholder {
  color: var(--text-gray);
}

.form-input:focus,
.form-textarea:focus {
  outline: none;
  border-color: var(--primary-purple-light);
  box-shadow: 0 0 0 4px rgba(109, 40, 217, 0.15);
  background: rgba(255, 255, 255, 0.05);
}

.form-textarea {
  min-height: 100px;
  resize: vertical;
}

.btn {
  background: linear-gradient(135deg, var(--accent-gold) 0%, var(--accent-gold-light) 100%);
  color: #111827;
  border: none;
  padding: 16px 32px;
  font-size: 16px;
  font-weight: 700;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 100%;
  font-family: 'Inter', sans-serif;
}

.btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(245, 158, 11, 0.3);
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

/* ============================================
   FOOTER STYLES
   ============================================ */
.footer {
  background: linear-gradient(180deg, #111827 0%, #0a0a12 100%);
  color: var(--text-white);
  padding: 80px 40px 40px;
  position: relative;
  border-top: 1px solid var(--card-border);
}

.footer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, var(--primary-purple), var(--accent-gold), var(--primary-purple), transparent);
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
}

.footer-section h3 {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 20px;
  color: var(--text-white);
}

.footer-section p {
  color: var(--text-gray);
  line-height: 1.8;
  font-size: 15px;
}

.footer-section a {
  display: block;
  color: var(--text-gray);
  text-decoration: none;
  margin-bottom: 12px;
  transition: all 0.3s ease;
  font-size: 15px;
}

.footer-section a:hover {
  color: var(--accent-gold);
  transform: translateX(5px);
}

.footer-bottom {
  max-width: 1200px;
  margin: 60px auto 0;
  padding-top: 30px;
  border-top: 1px solid var(--card-border);
  text-align: center;
  color: var(--text-gray);
  font-size: 14px;
}

/* ============================================
   SUCCESS MESSAGE
   ============================================ */
.success-message {
  background: linear-gradient(135deg, rgba(16, 185, 129, 0.2) 0%, rgba(5, 150, 105, 0.2) 100%);
  border: 1px solid rgba(16, 185, 129, 0.3);
  color: #34d399;
  padding: 20px;
  border-radius: 12px;
  margin-bottom: 24px;
  text-align: center;
  font-weight: 600;
  font-size: 15px;
}

/* ============================================
   ADMIN STYLES
   ============================================ */
.admin-container {
  min-height: 100vh;
  background: #111827;
  padding: 120px 40px 40px;
}

.admin-header {
  max-width: 1400px;
  margin: 0 auto 40px;
}

.admin-title {
  font-size: 42px;
  font-weight: 800;
  color: var(--text-white);
  margin-bottom: 12px;
}

.admin-subtitle {
  font-size: 16px;
  color: var(--text-gray);
}

.admin-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 24px;
  max-width: 1400px;
  margin: 0 auto 40px;
}

.stats-card {
  background: var(--card-bg);
  backdrop-filter: blur(10px);
  border: 1px solid var(--card-border);
  padding: 32px;
  border-radius: 20px;
  transition: all 0.3s ease;
}

.stats-card:hover {
  transform: translateY(-5px);
  border-color: var(--primary-purple-light);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3);
}

.stats-label {
  font-size: 14px;
  color: var(--text-gray);
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 12px;
}

.stats-value {
  font-size: 42px;
  font-weight: 800;
  background: linear-gradient(135deg, var(--text-white) 0%, var(--accent-gold-light) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.data-table {
  max-width: 1400px;
  margin: 0 auto;
  background: var(--card-bg);
  backdrop-filter: blur(10px);
  border: 1px solid var(--card-border);
  border-radius: 20px;
  overflow: hidden;
}

.table-header {
  padding: 28px;
  border-bottom: 1px solid var(--card-border);
}

.table-title {
  font-size: 22px;
  font-weight: 700;
  color: var(--text-white);
}

.table-wrapper {
  overflow-x: auto;
}

table {
  width: 100%;
  border-collapse: collapse;
}

table th {
  text-align: left;
  padding: 20px 28px;
  font-size: 13px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: var(--text-gray);
  border-bottom: 1px solid var(--card-border);
}

table td {
  padding: 20px 28px;
  font-size: 15px;
  color: var(--text-light);
  border-bottom: 1px solid var(--card-border);
}

table tr:last-child td {
  border-bottom: none;
}

table tr:hover td {
  background: rgba(255, 255, 255, 0.02);
}

/* ============================================
   RESPONSIVE STYLES
   ============================================ */
@media (max-width: 1024px) {
  .hero h1 {
    font-size: 48px;
  }
  
  .section-title {
    font-size: 36px;
  }
  
  .products-grid {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    gap: 24px;
  }
}

@media (max-width: 768px) {
  .header-content {
    padding: 0 20px;
  }
  
  .nav {
    gap: 20px;
  }
  
  .hero {
    padding: 140px 20px 80px;
  }
  
  .hero h1 {
    font-size: 36px;
  }
  
  .hero p {
    font-size: 16px;
  }
  
  .section {
    padding: 60px 20px;
  }
  
  .section-title {
    font-size: 28px;
  }
  
  .section-subtitle {
    font-size: 16px;
  }
  
  .products-grid {
    grid-template-columns: 1fr;
  }
  
  .modal {
    border-radius: 16px;
  }
  
  .modal-content {
    padding: 24px;
  }
}

@media (max-width: 480px) {
  .logo {
    font-size: 20px;
  }
  
  .nav a {
    font-size: 13px;
  }
  
  .hero h1 {
    font-size: 28px;
  }
  
  .btn-primary,
  .btn-secondary {
    padding: 12px 24px;
    font-size: 14px;
  }
}
