DOTS & ORBS\n   ============================================ */\n.background-animation {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  pointer-events: none;\n  z-index: 0;\n  overflow: hidden;\n}\n\n/* Animated Dots */\n.dot {\n  position: absolute;\n  width: 4px;\n  height: 4px;\n  background: rgba(255, 255, 255, 0.3);\n  border-radius: 50%;\n  animation: float-dot 20s infinite ease-in-out;\n}\n\n.dot:nth-child(1) { left: 10%; top: 20%; animation-delay: 0s; animation-duration: 25s; }\n.dot:nth-child(2) { left: 20%; top: 80%; animation-delay: 2s; animation-duration: 22s; }\n.dot:nth-child(3) { left: 30%; top: 40%; animation-delay: 4s; animation-duration: 28s; }\n.dot:nth-child(4) { left: 40%; top: 60%; animation-delay: 1s; animation-duration: 24s; }\n.dot:nth-child(5) { left: 50%; top: 30%; animation-delay: 3s; animation-duration: 26s; }\n.dot:nth-child(6) { left: 60%; top: 70%; animation-delay: 5s; animation-duration: 23s; }\n.dot:nth-child(7) { left: 70%; top: 50%; animation-delay: 2s; animation-duration: 27s; }\n.dot:nth-child(8) { left: 80%; top: 20%; animation-delay: 4s; animation-duration: 21s; }\n.dot:nth-child(9) { left: 90%; top: 90%; animation-delay: 1s; animation-duration: 29s; }\n.dot:nth-child(10) { left: 15%; top: 50%; animation-delay: 3s; animation-duration: 25s; }\n.dot:nth-child(11) { left: 25%; top: 10%; animation-delay: 2s; animation-duration: 24s; }\n.dot:nth-child(12) { left: 35%; top: 90%; animation-delay: 5s; animation-duration: 26s; }\n.dot:nth-child(13) { left: 45%; top: 15%; animation-delay: 1s; animation-duration: 22s; }\n.dot:nth-child(14) { left: 55%; top: 85%; animation-delay: 4s; animation-duration: 28s; }\n.dot:nth-child(15) { left: 65%; top: 35%; animation-delay: 3s; animation-duration: 23s; }\n.dot:nth-child(16) { left: 75%; top: 65%; animation-delay: 2s; animation-duration: 25s; }\n.dot:nth-child(17) { left: 85%; top: 45%; animation-delay: 5s; animation-duration: 27s; }\n.dot:nth-child(18) { left: 95%; top: 75%; animation-delay: 1s; animation-duration: 24s; }\n.dot:nth-child(19) { left: 5%; top: 35%; animation-delay: 3s; animation-duration: 26s; }\n.dot:nth-child(20) { left: 50%; top: 50%; animation-delay: 0s; animation-duration: 30s; }\n\n@keyframes float-dot {\n  0%, 100% {\n    transform: translate(0, 0) scale(1);\n    opacity: 0.3;\n  }\n  25% {\n    transform: translate(30px, -50px) scale(1.5);\n    opacity: 0.6;\n  }\n  50% {\n    transform: translate(-20px, -100px) scale(1);\n    opacity: 0.3;\n  }\n  75% {\n    transform: translate(50px, -50px) scale(1.2);\n    opacity: 0.5;\n  }\n}\n\n/* Large glowing orbs in background */\n.orb {\n  position: absolute;\n  border-radius: 50%;\n  filter: blur(80px);\n  opacity: 0.4;\n  animation: orb-float 15s infinite ease-in-out;\n}\n\n.orb-1 {\n  width: 400px;\n  height: 400px;\n  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);\n  top: -100px;\n  left: -100px;\n  animation-delay: 0s;\n}\n\n.orb-2 {\n  width: 300px;\n  height: 300px;\n  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);\n  bottom: -50px;\n  right: -50px;\n  animation-delay: -5s;\n}\n\n.orb-3 {\n  width: 250px;\n  height: 250px;\n  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);\n  top: 50%;\n  left: 50%;\n  animation-delay: -10s;\n}\n\n@keyframes orb-float {\n  0%, 100% {\n    transform: translate(0, 0);\n  }\n  33% {\n    transform: translate(30px, -30px);\n  }\n  66% {\n    transform: translate(-20px, 20px);\n  }\n}\n\n/* ============================================\n   CUSTOM ANIMATIONS\n   ============================================ */\n@keyframes float {\n  0%, 100% { transform: translateY(0px) rotate(0deg); }\n  50% { transform: translateY(-20px) rotate(5deg); }\n}\n\n@keyframes float-delayed {\n  0%, 100% { transform: translateY(0px) rotate(0deg); }\n  50% { transform: translateY(-15px) rotate(-3deg); }\n}\n\n@keyframes fade-in {\n  from { opacity: 0; transform: translateY(30px); }\n  to { opacity: 1; transform: translateY(0); }\n}\n\n@keyframes fade-in-up {\n  from { opacity: 0; transform: translateY(40px); }\n  to { opacity: 1; transform: translateY(0); }\n}\n\n@keyframes fade-in-up-delayed {\n  from { opacity: 0; transform: translateY(50px); }\n  to { opacity: 1; transform: translateY(0); }\n}\n\n@keyframes fade-in-up-more-delayed {\n  from { opacity: 0; transform: translateY(60px); }\n  to { opacity: 1; transform: translateY(0); }\n}\n\n@keyframes spin-slow {\n  from { transform: rotate(0deg); }\n  to { transform: rotate(360deg); }\n}\n\n/* Animation Classes */\n.animate-float {\n  animation: float 6s ease-in-out infinite;\n}\n\n.animate-float-delayed {\n  animation: float-delayed 8s ease-in-out infinite;\n}\n\n.animate-fade-in {\n  animation: fade-in 1s ease-out;\n}\n\n.animate-fade-in-up {\n  animation: fade-in-up 1s ease-out 0.2s both;\n}\n\n.animate-fade-in-up-delayed {\n  animation: fade-in-up-delayed 1s ease-out 0.4s both;\n}\n\n.animate-fade-in-up-more-delayed {\n  animation: fade-in-up-more-delayed 1s ease-out 0.6s both;\n}\n\n.animate-spin-slow {\n  animation: spin-slow 20s linear infinite;\n}\n\n/* ============================================\n   SCROLLBAR STYLING\n   ============================================ */\n::-webkit-scrollbar {\n  width: 8px;\n}\n\n::-webkit-scrollbar-track {\n  background: #1f2937;\n}\n\n::-webkit-scrollbar-thumb {\n  background: #6b46c1;\n  border-radius: 4px;\n}\n\n::-webkit-scrollbar-thumb:hover {\n  background: #7c3aed;\n}\n\n/* ============================================\n   SELECTION STYLING\n   ============================================ */\n::selection {\n  background: #6b46c1;\n  color: white;\n}\n\n/* ============================================\n   CUSTOM GRADIENT BACKGROUNDS\n   ============================================ */\n.bg-cosmic {\n  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);\n}\n\n.bg-mystic {\n  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);\n}\n\n/* ============================================\n   GLASS MORPHISM EFFECT\n   ============================================ */\n.glass {\n  background: rgba(255, 255, 255, 0.1);\n  backdrop-filter: blur(10px);\n  border: 1px solid rgba(255, 255, 255, 0.2);\n}\n\n/* ============================================\n   HOVER EFFECTS\n   ============================================ */\n.hover-lift {\n  transition: transform 0.3s ease, box-shadow 0.3s ease;\n}\n\n.hover-lift:hover {\n  transform: translateY(-5px);\n  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);\n}\n\n/* ============================================\n   SECURITY INDICATORS\n   ============================================ */\n.security-badge {\n  position: relative;\n  overflow: hidden;\n}\n\n.security-badge::before {\n  content: '';\n  position: absolute;\n  top: -50%;\n  left: -50%;\n  width: 200%;\n  height: 200%;\n  background: linear-gradient(45deg, transparent, rgba(34, 197, 94, 0.1), transparent);\n  animation: security-scan 3s infinite;\n}\n\n@keyframes security-scan {\n  0% { transform: rotate(0deg); }\n  100% { transform: rotate(360deg); }\n}\n\n/* ============================================\n   HEADER STYLES\n   ============================================ */\n.header {\n  background: rgba(17, 24, 39, 0.9);\n  backdrop-filter: blur(20px);\n  color: var(--text-white);\n  padding: 20px 0;\n  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.3);\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  z-index: 1000;\n  border-bottom: 1px solid rgba(255, 255, 255, 0.05);\n}\n\n.header-content {\n  max-width: 1400px;\n  margin: 0 auto;\n  padding: 0 40px;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.logo {\n  font-size: 26px;\n  font-weight: 800;\n  text-decoration: none;\n  color: var(--text-white);\n  display: flex;\n  align-items: center;\n  gap: 12px;\n  background: linear-gradient(135deg, var(--accent-gold) 0%, var(--accent-gold-light) 100%);\n  -webkit-background-clip: text;\n  -webkit-text-fill-color: transparent;\n  background-clip: text;\n}\n\n.logo span {\n  background: linear-gradient(135deg, var(--accent-gold) 0%, var(--accent-gold-light) 100%);\n  -webkit-background-clip: text;\n  -webkit-text-fill-color: transparent;\n  background-clip: text;\n}\n\n.nav {\n  display: flex;\n  gap: 40px;\n  align-items: center;\n}\n\n.nav a {\n  color: var(--text-gray);\n  text-decoration: none;\n  font-weight: 600;\n  font-size: 15px;\n  transition: all 0.3s ease;\n  position: relative;\n}\n\n.nav a::after {\n  content: '';\n  position: absolute;\n  bottom: -4px;\n  left: 0;\n  width: 0;\n  height: 2px;\n  background: linear-gradient(90deg, var(--accent-gold), var(--accent-gold-light));\n  transition: width 0.3s ease;\n}\n\n.nav a:hover {\n  color: var(--text-white);\n}\n\n.nav a:hover::after {\n  width: 100%;\n}\n\n.nav a:last-child {\n  background: linear-gradient(135deg, var(--primary-purple) 0%, var(--primary-purple-light));\n  padding: 10px 24px;\n  border-radius: 25px;\n  color: var(--text-white);\n}\n\n.nav a:last-child::after {\n  display: none;\n}\n\n.nav a:last-child:hover {\n  transform: translateY(-2px);\n  box-shadow: 0 8px 25px rgba(109, 40, 217, 0.4);\n}\n\n/* ============================================\n   HERO SECTION\n   ============================================ */\n.hero {\n  /* background: linear-gradient(135deg, #111827 0%, #1a1a3a 50%, #111827 100%); */\n  color: var(--text-white);\n  padding: 180px 40px 120px;\n  text-align: center;\n  position: relative;\n  overflow: hidden;\n}\n\n.hero::before {\n  content: '';\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background: \n    radial-gradient(ellipse at 20% 50%, rgba(109, 40, 217, 0.15) 0%, transparent 50%),\n    radial-gradient(ellipse at 80% 50%, rgba(245, 158, 11, 0.1) 0%, transparent 50%);\n  pointer-events: none;\n}\n\n.hero::after {\n  content: '';\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  /* background: url(\"data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23ffffff' fill-opacity='0.02'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E\"); */\n  pointer-events: none;\n}\n\n.hero-content {\n  position: relative;\n  z-index: 1;\n  max-width: 900px;\n  margin: 0 auto;\n}\n\n.hero h1 {\n  font-size: 64px;\n  font-weight: 800;\n  margin-bottom: 24px;\n  line-height: 1.1;\n  background: linear-gradient(135deg, var(--text-white) 0%, var(--accent-gold-light) 50%, var(--text-white) 100%);\n  -webkit-background-clip: text;\n  -webkit-text-fill-color: transparent;\n  background-clip: text;\n  animation: shimmer 3s ease-in-out infinite;\n  background-size: 200% auto;\n}\n\n@keyframes shimmer {\n  0%, 100% { background-position: 0% 50%; }\n  50% { background-position: 100% 50%; }\n}\n\n.hero p {\n  font-size: 20px;\n  max-width: 700px;\n  margin: 0 auto 40px;\n  opacity: 0.9;\n  line-height: 1.8;\n  color: var(--text-gray);\n}\n\n.hero-buttons {\n  display: flex;\n  gap: 20px;\n  justify-content: center;\n  flex-wrap: wrap;\n}\n\n.btn-primary {\n  background: linear-gradient(135deg, var(--primary-purple) 0%, var(--primary-purple-light) 100%);\n  color: var(--text-white);\n  border: none;\n  padding: 16px 40px;\n  font-size: 16px;\n  font-weight: 700;\n  border-radius: 30px;\n  cursor: pointer;\n  transition: all 0.3s ease;\n  font-family: 'Inter', sans-serif;\n}\n\n.btn-primary:hover {\n  transform: translateY(-3px);\n  box-shadow: 0 15px 40px rgba(109, 40, 217, 0.4);\n}\n\n.btn-secondary {\n  background: transparent;\n  color: var(--text-white);\n  border: 2px solid rgba(255, 255, 255, 0.2);\n  padding: 14px 38px;\n  font-size: 16px;\n  font-weight: 600;\n  border-radius: 30px;\n  cursor: pointer;\n  transition: all 0.3s ease;\n  font-family: 'Inter', sans-serif;\n}\n\n.btn-secondary:hover {\n  background: rgba(255, 255, 255, 0.05);\n  border-color: var(--accent-gold);\n}\n\n/* Floating elements in hero */\n.floating-element {\n  position: absolute;\n  opacity: 0.15;\n  pointer-events: none;\n}\n\n.floating-element-1 {\n  top: 20%;\n  left: 10%;\n  width: 60px;\n  height: 60px;\n  border: 2px solid rgba(255, 255, 255, 0.3);\n  border-radius: 50%;\n  animation: float 6s ease-in-out infinite;\n}\n\n.floating-element-2 {\n  top: 30%;\n  right: 15%;\n  width: 40px;\n  height: 40px;\n  background: linear-gradient(135deg, rgba(102, 126, 234, 0.3), rgba(118, 75, 162, 0.3));\n  border-radius: 8px;\n  animation: float-delayed 8s ease-in-out infinite;\n}\n\n.floating-element-3 {\n  bottom: 25%;\n  left: 20%;\n  width: 80px;\n  height: 80px;\n  border: 2px solid rgba(245, 158, 11, 0.3);\n  border-radius: 50%;\n  animation: float 7s ease-in-out infinite 1s;\n}\n\n.floating-element-4 {\n  bottom: 35%;\n  right: 10%;\n  width: 50px;\n  height: 50px;\n  background: linear-gradient(135deg, rgba(240, 147, 251, 0.3), rgba(245, 87, 108, 0.3));\n  border-radius: 12px;\n  animation: float-delayed 9s ease-in-out infinite 0.5s;\n}\n\n/* ============================================\n   SECTION STYLES\n   ============================================ */\n.section {\n  max-width: 1400px;\n  margin: 0 auto;\n  padding: 100px 40px;\n  position: relative;\n  z-index: 1;\n}\n\n.section-header {\n  text-align: center;\n  margin-bottom: 60px;\n}\n\n.section-title {\n  font-size: 48px;\n  font-weight: 800;\n  margin-bottom: 16px;\n  color: var(--text-white);\n  position: relative;\n  display: inline-block;\n}\n\n.section-title::after {\n  content: '';\n  position: absolute;\n  bottom: -8px;\n  left: 50%;\n  transform: translateX(-50%);\n  width: 60px;\n  height: 4px;\n  background: linear-gradient(90deg, var(--accent-gold), var(--accent-gold-light));\n  border-radius: 2px;\n}\n\n.section-subtitle {\n  font-size: 18px;\n  color: var(--text-gray);\n  max-width: 700px;\n  margin: 24px auto 0;\n  line-height: 1.8;\n}\n\n/* ============================================\n   PRODUCTS GRID\n   ============================================ */\n.products-grid {\n  display: grid;\n  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));\n  gap: 32px;\n}\n\n.product-card {\n  background: var(--card-bg);\n  backdrop-filter: blur(10px);\n  border: 1px solid var(--card-border);\n  border-radius: 20px;\n  overflow: hidden;\n  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);\n  cursor: pointer;\n  position: relative;\n}\n\n.product-card::before {\n  content: '';\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background: linear-gradient(135deg, rgba(109, 40, 217, 0.1) 0%, rgba(245, 158, 11, 0.05) 100%);\n  opacity: 0;\n  transition: opacity 0.4s ease;\n}\n\n.product-card:hover {\n  transform: translateY(-12px);\n  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.4);\n  border-color: var(--primary-purple-light);\n}\n\n.product-card:hover::before {\n  opacity: 1;\n}\n\n.product-image {\n  width: 100%;\n  height: 260px;\n  object-fit: cover;\n  transition: transform 0.4s ease;\n}\n\n.product-card:hover .product-image {\n  transform: scale(1.05);\n}\n\n.product-content {\n  padding: 28px;\n  position: relative;\n  z-index: 1;\n}\n\n.product-category {\n  font-size: 13px;\n  font-weight: 700;\n  text-transform: uppercase;\n  letter-spacing: 2px;\n  color: var(--accent-gold);\n  margin-bottom: 12px;\n}\n\n.product-title {\n  font-size: 22px;\n  font-weight: 700;\n  margin-bottom: 12px;\n  color: var(--text-white);\n  line-height: 1.3;\n}\n\n.product-description {\n  font-size: 15px;\n  color: var(--text-gray);\n  margin-bottom: 20px;\n  line-height: 1.7;\n  display: -webkit-box;\n  -webkit-line-clamp: 2;\n  -webkit-box-orient: vertical;\n  overflow: hidden;\n}\n\n.product-footer {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.product-price {\n  font-size: 28px;\n  font-weight: 800;\n  background: linear-gradient(135deg, var(--accent-gold) 0%, var(--accent-gold-light) 100%);\n  -webkit-background-clip: text;\n  -webkit-text-fill-color: transparent;\n  background-clip: text;\n}\n\n.product-btn {\n  background: linear-gradient(135deg, var(--primary-purple) 0%, var(--primary-purple-light));\n  color: var(--text-white);\n  border: none;\n  padding: 12px 24px;\n  font-size: 14px;\n  font-weight: 600;\n  border-radius: 25px;\n  cursor: pointer;\n  transition: all 0.3s ease;\n}\n\n.product-btn:hover {\n  transform: scale(1.05);\n  box-shadow: 0 8px 20px rgba(109, 40, 217, 0.4);\n}\n\n/* ============================================\n   MODAL STYLES\n   ============================================ */\n.modal-overlay {\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background: rgba(0, 0, 0, 0.85);\n  backdrop-filter: blur(10px);\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  z-index: 2000;\n  padding: 24px;\n  animation: fadeIn 0.3s ease;\n}\n\n@keyframes fadeIn {\n  from { opacity: 0; }\n  to { opacity: 1; }\n}\n\n.modal {\n  background: var(--card-bg);\n  backdrop-filter: blur(20px);\n  border: 1px solid var(--card-border);\n  border-radius: 24px;\n  max-width: 520px;\n  width: 100%;\n  max-height: 90vh;\n  overflow-y: auto;\n  position: relative;\n  animation: slideUp 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);\n}\n\n@keyframes slideUp {\n  from {\n    opacity: 0;\n    transform: translateY(60px) scale(0.95);\n  }\n  to {\n    opacity: 1;\n    transform: translateY(0) scale(1);\n  }\n}\n\n.modal-close {\n  position: absolute;\n  top: 20px;\n  right: 20px;\n  background: rgba(255, 255, 255, 0.05);\n  border: 1px solid var(--card-border);\n  width: 40px;\n  height: 40px;\n  border-radius: 50%;\n  cursor: pointer;\n  font-size: 24px;\n  color: var(--text-gray);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  transition: all 0.3s ease;\n  z-index: 